<template>
  <div id="app">
    <div>
      <div
        class="bg-green-200 text-sm w-2/3 m-2 p-2 rounded"
        v-for="(msg, i) in messages"
        :key="i"
      >
        <h6 class="font-semibold">{{ msg.username }}</h6>
        <p>{{ msg.msg }}</p>
      </div>
    </div>
    <div class="w-full flex justify-center items-center fixed bottom-0">
      <input
        class="w-4/5 h-12 border-t-2 border-gray-600 p-2 text-xs outline-none"
        type="text"
        v-model.trim="msg"
        v-on:keyup.enter="send"
      />
      <button
        class="w-1/5 h-12 bg-gray-800 text-white font-semibold"
        @click="send"
      >
        send
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      messages: [],
      msg: "",
    };
  },
  methods: {
    incoming(data) {
      this.messages.push(data);
    },
    send() {
      this.$socket.client.emit("chat message", this.msg);
      this.msg = "";
    },
  },
  sockets: {
    connect() {
      console.log("socket connected");
    },
  },
  mounted() {
    this.$socket.client.on("chat message", this.incoming);
  },
};
</script>
